<template>
  <div class="call-paper">
    <div class="call-content">
      <p class="title">Call for Paper</p>
      <div class="download">
        Download CFP: <a target="_blank" href="/static/CFP.pdf" download="cfp.pdf">[PDF]</a>
      </div>
      <p class="content">
        The Sixth International Conference on Frontiers in Cyber Security (FCS 2023) will be held in Chengdu, P.R. China from August 24 to August 26, 2023, organized by Cyber Security School, University of Electronic Science and Technology of China (UESTC). The cyber security is paid more and more attention by industry and academia. New cyber security technologies are emerging. Many universities and institutions have set up the research center or school for cyber security. Both government and enterprises are strongly supporting teaching and research in the cyber security. However, how to carry out research and education on cyber security is inexperienced. How to run the center for cyber security is also worth exploring since new center usually involves many departments. The FCS conference provides a good platform for researchers and practitioners to exchange the latest research results in cyber security. The conference proceedings will be published by Springer Communications in Computer and Information Science (CCIS) which is indexed by EI.
        </p>
    </div>
    <a-instructions></a-instructions>
    <area-interest></area-interest>
  </div>
</template>

<script>
  import AInstructions from '../author-instruction/a-instructions.vue'
  import AreaInterest from '../area-interest/area-interest.vue'
  export default {
    components: {
      AInstructions,
      AreaInterest
    }
  }
</script>

<style lang="stylus" scoped>
  .call-paper
    margin 10px auto
    box-sizing border-box
    border 1px solid #ccc
    width 70%;
    background-color #ddd
    border-radius 4px
    padding 10px
    .call-content
      .download
        font-size 18px
        padding 10px
        margin 10px 20px
        border-top 1px solid #000
        border-bottom 1px solid #000
      .title
        font-size 24px
        line-height 36%
        text-align center
        padding 10px
      .content
        font-size 18px
        line-height 24px
        width 95%
        margin 10px auto
        text-align left
        text-indent 2em
</style>


