<template>
  <div class="important-date">
    <p class="title">{{title}}</p>
    <p class="detail-date">
      Paper Submission Due: Jun 15, 2023 (anywhere on earth)
    </p>
     <p class="detail-date">
       Acceptance Notification: Jul 15, 2023 (anywhere on earth)
    </p>
     <p class="detail-date">
       Camera Ready Due: Jul 31, 2023 (anywhere on earth)
    </p>
     <p class="detail-date">
       Conference Date: Aug 24-26, 2023 (anywhere on earth)
    </p>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        title: 'Important Date'
      }
    },
    computed: {
      detailDates() {
        let date = 'Paper Submission Due: 10 June 2023;Acceptance Notification: 10 Aug 2023;Camera Ready Due: Aug 20, 2023;Conference Date: 5-7 Nov 2023'
        return date.split(';')
      }
    },
  }
</script>

<style scoped>
.important-date {
  margin: 20px auto;
  width: 60%;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: #ddd;
  padding: 10px;
  text-align: center;
}
.title {
  font-size: 36px;
  line-height: 52px;
}
.detail-date {
  font-size: 14px;
  line-height: 21px;
}
.important {
  color: red;
  font-weight: bold;
}
</style>


