<template>
  <div class="accept-paper">
    <div class="paper">
      <h1 class="title">Special Issues</h1>
      <p>Presented papers will be considered for publication in some Special Issues in International Journals.</p>
      <li>Computer Communications (Accepted)</li>
      <li>Applied Sciences (Accepted)</li>
      <li>Journal of Systems Architecture (Accepted)</li>
      <li>Journal of Information Security and Applications (Already submitted)</li>
      <p>Our application for a special issue has been approved by Computer Communications (SCI, JCR 2)</p>
      <p>Our application for a special issue has been approved by Applied Sciences (SCI, JCR 2)</p>
      <p>Our application for a special issue has been approved by Journal of Systems Architecture (SCI, JCR 1)</p>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'AcceptPaper'
  }
</script>

<style lang="stylus" scoped>
  .accept-paper
    margin 10px auto
    box-sizing border-box
    border 1px solid #ccc
    width 70%;
    background-color #ddd
    border-radius 4px
    padding 10px
    .paper
      .title
        text-align center
        font-size 24px
        padding 10px 0
      P, li
        line-height 1.8
        margin 5px 0
        font-size 18px
</style>


