<template>
  <div class="header">
    <div class="title">{{title}}</div>
    <div v-if="isHome">
      <Button class="to-home" @click="toHome">Home</Button>
    </div>
    <p class="subtitle">
      {{subTitle}} <br>
      Aug.24-Aug.26,Chengdu,China
    </p>
  </div>
</template>

<script>
  import {
    mapState
  } from 'vuex'
  
  export default {
    data() {
      return {
        title: 'FCS 2023',
        subTitle: 'The Sixth International Conference on Frontiers in Cyber Security'
      }
    },
    methods: {
      toHome() {
        this.$router.push({
          path: '/fcs'
        })
  
        this.$store.commit('navigation', false)
      }
    },
    created() {
  
    },
    computed: {
      ...mapState([
        'isHome'
      ])
    }
  }
</script>

<style scoped>
  .header {
    /* background-color: #333; */
    text-align: center;
    position: relative;
  }
  
  .title {
    color: #b8d9d5;
    font-size: 36px;
    line-height: 52px;
    padding-top: 20px;
  }
  
  .subtitle {
    color: #fff;
    font-size: 20px;
    line-height: 24px;
    margin-top: 0;
    padding-bottom: 20px;
  }
  
  .to-home {
    position: absolute;
    top: 15px;
    left: 5%;
  }
</style>


