import Vue from 'vue'
import Router from 'vue-router'

import MainContent from '../components/main-content/main-content.vue'
import AuthorInstruction from '../components/author-instruction/author-instruction.vue'
import AreaInterest from '../components/area-interest/area-interest.vue'
import CommitteeIndex from '../components/committee/committee-index.vue'
import ImportantDate from '../components/important-date/important-date.vue'
import ConferenceVenue from '../components/conference-venue/ConferenceVenue.vue'
import Accommodation from '../components/accommodation/Accommodation.vue'
import AboutChengdu  from '../components/about-chengdu/AboutChengdu.vue'
import ContactUs from '../components/contact-us/ContactUs.vue'
import Registration from '../components/registration/registration.vue'
import Program from '../components/program/program.vue'
import CallForPaper from '../components/call-for-paper/call-for-paper.vue'
import KeynoteSpeech from '../components/keynote-speech/keynote-speech.vue'
import SpecialIssues from '../components/special-issues/special-issues.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: '/',
  routes: [
    {
      path: '/',
      name: 'MainContent',
      component: MainContent
    },
    {
      path: '/author',
      name: 'AuthorInstruction',
      component: AuthorInstruction
    },
    {
      path: '/interest',
      name: 'AreaInterest',
      component: AreaInterest
    },
    {
      path: '/committee',
      name: 'Committee',
      component: CommitteeIndex
    },
    {
      path: '/date',
      name: 'ImportantDate',
      component: ImportantDate
    },
    {
      path: '/conference',
      name: 'ConferenceVenue',
      component: ConferenceVenue
    },
    {
      path: '/accommodation',
      name: 'Accommodation',
      component: Accommodation
    },
    {
      path: '/about',
      name: 'AboutChengdu',
      component: AboutChengdu
    },
    {
      path: '/contact',
      name: 'ContactUs',
      component: ContactUs
    },
    {
      path: '/registration',
      name: 'Registration',
      component: Registration
    },
    {
      path: '/issue',
      name: 'SpecialIssues',
      component: SpecialIssues
    },
    {
      path: '/program',
      name: 'Program',
      component: Program
    },
    {
      path: '/call',
      name: 'CallForPaper',
      component: CallForPaper
    },
    {
      path: '/keynote',
      name: 'KeynoteSpeech',
      component: KeynoteSpeech
    },
    {
      path: '*',
      redirect: '/'
    }
  ]
})



