<template>
  <div class="a-intructions">
    <p class="title">{{title}}</p>
    <p class="content" v-html="content"></p>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        title: 'Author Instructions',
        content: ' Authors are invited to submit original papers written in English. Each paper must not appear in any other conferences with proceedings or any journal. The submission must be anonymous, with no author names, affiliations, acknowledgements, or obvious references. Original contributions are invited up to 16 pages in length (single column) excluding appendices and bibliography and up to 20 pages in total, using at least 11-point fonts and with reasonable margins. It is highly recommended to prepare the papers in the Springer LNCS format <a target="_blank" href="http://www.springer.com/computer/lncs?SGWID=0-164-6-793341-0">http://www.springer.com/computer/lncs?SGWID=0-164-6-793341-0</a>. Only PDF files will be accepted. If accepted, one of the authors is expected to present the paper at the conference. Papers must be submitted through <a target="_blank" href="https://easychair.org/conferences/?conf=fcs2023">https://easychair.org/conferences/?conf=fcs2023</a>'
      }
    },
  }
</script>

<style scoped>
.a-intructions {
  margin: 10px auto;
  box-sizing: border-box;
  /* border: 1px solid #ccc; */
  width: 100%;
  background-color: #ddd;
  border-radius: 4px;
  padding: 10px;
}

.title {
  font-size: 24px;
  line-height: 36px;
  text-align: center;
}
.content {
  font-size: 18px;
  line-height: 24px;
  text-align: left;
  text-indent:2em;
  width: 95%;
  margin: 10px auto;
}
</style>


