<template>
  <div id="app">
    <fcs-header></fcs-header>
    <ul class="tab-router">
      <router-link to="/call" tag="li"><span @click="showHome" class="tab-font">Call For Paper</span></router-link>
      <router-link to="/committee" tag="li"><span @click="showHome" class="tab-font">Committee</span></router-link>
      <router-link to="/date" tag="li"><span @click="showHome" class="tab-font">Important Date</span></router-link>
      <router-link to="/conference" tag="li"><span @click="showHome" class="tab-font">Conference Venue</span></router-link>
      <router-link to="/registration" tag="li"><span @click="showHome" class="tab-font">Registration</span></router-link>
      <router-link to="/issue" tag="li"><span @click="showHome" class="tab-font">Special Issues</span></router-link>
      <router-link to="/program" tag="li"><span @click="showHome" class="tab-font">Program</span></router-link>
      <router-link to="/keynote" tag="li"><span @click="showHome" class="tab-font">Keynote Speech</span></router-link>
      <!-- <router-link to="/interest" tag="li"><span @click="showHome" class="tab-font">Area Of Interest</span></router-link> -->
      <!-- <router-link to="/accommodation" tag="li"><span @click="showHome" class="tab-font">Accommodation</span></router-link> -->
      <router-link to="/about" tag="li"><span @click="showHome" class="tab-font">About Chengdu</span></router-link>
      <router-link to="/contact" tag="li"><span @click="showHome" class="tab-font">Contact Us</span></router-link>
    </ul>
    <!-- <button @click="mutedChange">123123</button> -->
    <router-view></router-view>
    <audio controls muted autoplay preload="auto" class="aud" src="../static/chengdu.mp3"></audio>
  </div>

</template>

<script>

import Header from './components/header/header'

export default {
  name: 'app',
  components: {
    'fcs-header': Header
  },
  data() {
    return {
      muted: true
    }
  },
  methods: {
    showHome() {
      this.$store.commit('navigation', true)
    },
    // mutedChange() {
    //   console.log(123);
    //   this.$refs.audioPlayer.muted = false
    // }
  },
}
</script>

<style>
body {
  background: url('./assets/images/raw_1509456463.png') no-repeat;
  background-size: cover;
  background-attachment: fixed;
}

.aud {
  z-index: 99;
  position: fixed;
  left: 0;
  right: 0;
  margin: auto;
  bottom: 50px;
}

.router {
  background-color: #000;
}
.tab-router {
  width: 100%;
  display: flex;
  margin: auto;
  justify-content: space-around;
  text-align: center;
  background-color: #000;
  color: #fff;
  padding: 10px 0;
  text-align: center;
}

.tab-router li {
  cursor: pointer;
  line-height: 24px;
  flex: 1;
}
.tab-font { 
  font-family: 'Microsoft YaHei'; 
  font-size: 1em;
  line-height: 1em;
  display: inline-block;
}
.router-link-active {
  background-color: #fff;
  color: #000;
}
</style>



